@import "../../styles/variables";
@import "../../styles/media";

.item {
  min-height: $cell * 12;
  padding: $cell * 4 $cell * 2;
  margin: -1px 0;
  border: 1px solid transparent;
  background: transparent;
  display: flex;
  align-items: center;
  gap: $cell * 10;
  box-sizing: border-box;
  transition: all $short ease;
  position: relative;
  &:first-of-type {
    margin-top: 0; }
  &:last-of-type {
    margin-bottom: 0; } }

.changing {
  padding-left: $cell * 4;
  background: rgba($brightBlue, 0.04);
  border-left-color: $brightBlue;
  .changingField {
    font-weight: bold;
    height: auto;
    background: $white;
    border: 1px solid $brightBlue; } }

.parameters {
  display: flex;
  gap: $cell * 5;
  flex-grow: 1; }

.parameter {
  font-size: 13px;
  color: $darkBlue;
  margin-bottom: $cell;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  span {
    font-weight: bold; }
  &:last-of-type {
    margin-bottom: 0; } }

.fixed {
  width: 200px;
  span {
    flex-shrink: 0; }
  > input {
      width: 100%; } }

.large {
  width: 400px;
  .changingField {
    width: 100%;
    padding-top: $cell * 3;
    padding-bottom: 0;
    resize: none; } }

.changingField {
  font-family: "RobotoReg";
  font-size: 13px;
  color: $mainBlack;
  padding: $cell * 2 $cell;
  margin: 0;
  margin-left: $cell * 2;
  line-height: 1;
  background: none;
  border-radius: 4px;
  border: 1px solid transparent;
  &:focus {
    outline: none; } }

.delete {
  font-size: 11px;
  line-height: 13px;
  color: $control;
  width: 70px;
  padding: 5.5px 7.5px 5.5px 20px;
  box-sizing: border-box;
  background: $white;
  border: 0.5px solid $crimson;
  border-radius: $borderRadiusSmall;
  cursor: pointer;
  position: relative;
  transition: all $short ease;
  position: relative;
  svg {
    position: absolute;
    top: -2px;
    left: 0px;
    opacity: 0.3;
    transform: scale(0.4);
    path {
      fill: $crimson;
      transition: all $short ease; } }
  &:hover {
    background: $crimson;
    color: $white;
    z-index: 3;
    svg {
      opacity: 1;
      path {
        fill: $white; } } } }

@include desktopMinDevice {
  .item {
    min-height: 0;
    margin: 0;
    border: none;
    border-left: 5px solid transparent;
    &:last-of-type {
      border-bottom: none; } }
  .changing {
    border-radius: 0;
    border-left-color: $brightBlue; }
  .parameters {
    flex-direction: column;
    gap: 0; }
  .delete {
    height: 40px;
    svg {
      top: 6px; } } }

@include mobileDevice {
  .item {
    border: 1px solid $ultraLightGrey;
    flex-direction: column;
    grid-gap: 0;
    margin: -1px 0;
    position: relative;
    &:first-of-type {
      margin-top: 0; }
    &:last-of-type {
      margin-bottom: 0;
      border-bottom: 1px solid $ultraLightGrey; } }
  .changing {
    border-left: 1px solid $brightBlue; }
  .parameters {
    width: 100%; }
  .large {
    width: 100%;
    .changingField {
      height: 32px; } }
  .parameter {
    span {
      font-size: 13px; } }
  .changingField {
    font-size: 13px;
    line-height: 1; }
  .delete {
    position: absolute;
    right: $cell * 2;
    top: $cell * 2;
    height: 25px;
    background: transparent;
    // width: 25px
    // font-size: 0
    // line-height: 0
    border: none;
    span {
 }      // display: none
    svg {
      opacity: 1;
      top: 0;
      path {
        fill: $crimson; } } } }

