@import "../../../styles/variables";
@import "../../../styles/media";

.tabs {
  padding: 0;
  margin: 0;
  margin-bottom: $cell * 5;
  display: flex;
  width: 100%; }


.item {
  list-style-type: none;
  width: calc(100% / 6);
  box-sizing: border-box;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: rgba($brightBlue, 0.1); }
  &:hover {
    &::after {
      background-color: rgba($brightBlue, 0.4); }
    .button {
      color: $brightBlue; } } }

.active {
  &::after {
    background-color: $brightBlue; }
  .button {
    color: $brightBlue; }
  &:hover {
    &::after {
      background-color: $brightBlue; } } }

.button {
  font-family: "RobotoReg";
  font-size: 14px;
  font-weight: bold;
  color: $mainBlack;
  width: 100%;
  height: 100%;
  border: none;
  background: none;
  padding: $cell * 2;
  cursor: pointer; }

@include mobileDevice {
  .tabs {
    background-color: rgba($brightBlue, 0.1);
    border-radius: $borderRadiusBig;
    padding: $cell;
    padding-top: 0;
    row-gap: $cell * 2;
    flex-wrap: wrap; }
  .item {
    width: auto;
    &::after {
      height: 2px;
      top: auto;
      bottom: -3px;
      background: transparent; }
    &:hover {
      &::after {
        background: transparent; } } }
  .active.item {
    &::after {
      background-color: $brightBlue; } }
  .button {
    font-size: 14px;
    padding-top: $cell * 3;
    padding-bottom: $cell; } }



